<template>
  <div>
    <el-row :gutter="10" class="exchange-lists">
      <el-col :span="4">调出部门</el-col>
      <el-col :span="4">调出项目</el-col>
      <el-col :span="4">调出金额</el-col>
      <el-col :span="4">调人部门</el-col>
      <el-col :span="4">调入项目</el-col>
    </el-row>
    <el-row :gutter="10" class="exchange-lists" v-for="(exchange, $outIndex) in exchanges" :key="'out-' + $outIndex">
      <el-col :span="4">
        <el-select v-model="exchange.from.code" placeholder="调出部门" @change="changeCode($outIndex, 'from')">
          <el-option :label="group.name" :value="group.code" v-for="(group, $inIndex) in grouplists" :key="'fromgroup-' + $outIndex + '-' + $inIndex "></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-select v-model="exchange.from.catalog" placeholder="调出项目">
          <el-option :label="type.name" :value="type.code" v-for="(type, $inIndex) in typeLists" :key="'fromtype-' + $outIndex + '-' + $inIndex "></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-input v-model="exchange.from.amount" min="0" type="number" placeholder="调出金额(元)"></el-input>
      </el-col>
      <el-col :span="4">
        <el-select v-model="exchange.to.code" placeholder="调入部门" @change="changeCode($outIndex, 'to')">
          <el-option :label="group.name" :value="group.code" v-for="(group, $inIndex) in grouplists" :key="'togroup-' + $outIndex + '-' + $inIndex " ></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-select v-model="exchange.to.catalog" placeholder="调入项目">
          <el-option :label="type.name" :value="type.code" v-for="(type, $inIndex) in typeLists" :key="'totype-' + $outIndex + '-' + $inIndex "></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-button  plain type="danger" @click="deleteExchange($outIndex)">删 除</el-button>
      </el-col>
    </el-row>
    <div style="margin-top:20px;">
      <el-button type="primary" plain @click="addExchange">新 增 调 整</el-button>
      <el-button type="success" plain @click="saveExchange">确 定 调 整</el-button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'exchangeview',
    data() {
      return {
        exchanges: [],
        grouplists: [],
        typeLists: []
      }
    },
    methods: {
      deleteExchange(index) {
        this.$delete(this.exchanges, index);
      },
      initExchange() {
        year: new Date().getFullYear(),
        this.exchanges = [{
          from: {
            code: '',
            name: '',
            catalog: '',
            amount: 0
          },
          to: {
            code: '',
            name: '',
            catalog: '',
            amount: ''
          }
        }]
      },
      addExchange() {
        this.exchanges.push({
          from: {
            code: '',
            name: '',
            catalog: '',
            amount: 0
          },
          to: {
            code: '',
            name: '',
            catalog: '',
            amount: ''
          }
        })
      },
      getGroupLists() {
        this.$http.get(`/ec/api/depts/grouplists`).then(res =>{
          let resData = res.data;
          if(resData.errcode === 0) {
            this.grouplists = resData.data || [];
          }
        })
      },
      getTypeLists() {
        this.$http.get(`/ec/api/types?type=budgets`).then(res => {
          let resData = res.data;
          if (resData.errcode === 0) {
            this.typeLists = resData.data || [];
          }
        })
      },
      validateExchange() {
        let valid = true;
        for(let exchange of this.exchanges) {
          let from = exchange.from;
          let to = exchange.to;
          if(!from.code || !from.amount || !from.catalog) {
            valid = false;
          }
          if(!to.code || !to.catalog) {
            valid = false;
          }
          if(from.code === to.code && from.catalog === to.catalog) {
            valid = false;
          }
          if(!valid) {
            break;
          }
        }
        return valid;
      },
      saveExchange() {
        let valid = this.validateExchange()
        console.log(valid)
        if(!valid) {
          this.$message(`调整数据不正确`)
          return;
        }
        this.$http.post('/ec/api/exchange/', {
          year: this.year,
          exchanges: this.exchanges
        }).then((res) => {
          let resData = res.data;
          if (resData.errcode === 0) {
            this.$message({message: '调整成功', type: 'success'})
            this.initExchange()
            return;
          }
          this.$message({message: '调整失败', type: 'error'})
        })
      },

      changeCode(index, type) {
        let groupIndex = this.grouplists.findIndex(item => {
          return item.code === this.exchanges[index][type].code;
        });
        
        this.exchanges[index][type].name = this.grouplists[groupIndex].name;
      },
    },
    created() {
      this.getGroupLists();
      this.getTypeLists()
      this.initExchange()
    }
  }
</script>

<style>
.exchange-lists {
  margin-top: 10px;
}
</style>
