<template>
	<div class="content">
		<el-table :data="records" border style="width: 100%; margin-top: 20px">
			<el-table-column  label="调出部门">
				<template slot-scope="scope">
					{{scope.row.from.name}}
				</template>
			</el-table-column>
			<el-table-column label="调出项目">
				<template slot-scope="scope">
					{{typeMap[scope.row.to.catalog]}}
				</template>
			</el-table-column>
			<el-table-column label="调出金额(元)">
				<template slot-scope="scope">
					{{scope.row.from.amount}}
				</template>
			</el-table-column>
			<el-table-column label="调人部门">
				<template slot-scope="scope">
					{{scope.row.to.name}}
				</template>
			</el-table-column>
			<el-table-column label="调入项目">
				<template slot-scope="scope">
					{{typeMap[scope.row.to.catalog]}}
				</template>
			</el-table-column>
			<el-table-column label="调整时间">
				<template slot-scope="scope">
					{{parseTime(scope.row.createTime)}}
				</template>
			</el-table-column>
		</el-table>
		<el-pagination :current-page="page" :page-size="15" layout="total, prev, pager, next, jumper" :total="count"
			align="right" @current-change="changePage" class="pagi">
		</el-pagination>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				page: 1,
				count: 0,
				limit: 10,
				yearValue: new Date(),
				records: [],
				typeMap: {
					trip: '差旅费',
					benefits: '福利费',
					others: '其他费用'
				},
			};
		},
		methods: {
			parseTime(date) {
				date = new Date(date)
				return `${date.getFullYear()}年${date.getMonth() +1}月${date.getDate()}日${date.getHours()}时${date.getMinutes()}分`
			},
			getTypeLists() {
        this.$http.get(`/ec/api/types?type=budgets`).then(res => {
          let resData = res.data;
          if (resData.errcode === 0) {
            for(let item of (resData.data || [])) {
							typeMap[item.type]  = item.name;
						}
          }
        })
      },
			getExchangeLists(keywords) {
				this.$http.get(`/ec/api/exchange?page=${this.page}&limit=${this.limit}&year=${this.year}&keywords=${this.keywords}`).then(res =>{
					let resData = res.data;
          if (resData.errcode === 0) {
            this.count = resData.data.count || 0;
						this.records = resData.data.rows ||[];
					}
				})
			},
			changePage(page) {
        this.page = page;
        this.getExchangeLists()
      }
		},
		created() {
			this.getTypeLists()
			this.getExchangeLists();
		}
	};
</script>
<style>
	.pagi {
		margin: 10px;
	}
</style>